html {
    scroll-behavior: smooth;
}


.top_banner {
    position: fixed;
    z-index: 99999;
    background-color: rgb(15, 15, 15);
    box-shadow: 0px 0px 10px 1px black;
    width: 100vw;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.nav_btn{
    color: white;
    text-decoration: none;
    font-weight: bold;
    letter-spacing: 2px;
}

.nav_btn:hover{
    color: rgb(255, 0, 255);
}

.hamburger {
    position: absolute;
    z-index: 99999999;
    right: 3rem;
    top: 5rem;
}

.hamburger_body {
    z-index: 999999;
    position: absolute;
    width: 100%;
    height: 100vh;
}

@media screen and (min-width:320px) and (max-width:480px) {
    .top_banner {
        background-color: transparent;
    }
    
    .nav_btn{
        font-size: 12px;
    }
    
    .nav_btn:hover{
        color: rgb(255, 0, 255);
    }  
    .hamburger {
    position: absolute;
    z-index: 99999999;
    right: 1.6rem;
    top: 3rem;
}
}


@media screen and (min-width:481px) and (max-width:768px) {
    .top_banner {
        background-color: transparent;
        justify-content: flex-end;
        height: 2.2rem;
    }
    .nav_btn {
        font-size: 14px;
        font-weight: bolder;
        padding: 8px 12px 8px 12px;
        transition-duration: 0.3s;
    }
    
    .nav_btn:hover{
        color: rgb(255, 0, 200);
        transition-duration: 0.3s;
        background-color: rgb(255, 0, 255, 0.1);
    }  

    .hamburger {
        position: absolute;
        z-index: 99999999;
        right: 1rem;
        top: 3rem;
    }
}

@media screen and (min-width:769px) and (max-width:1023px) {
    .top_banner {
        background-color: transparent;
        justify-content: flex-end;
        height: 2.2rem;
    }

    .nav_btn {
        font-size: 14px;
        font-weight: bolder;
        padding: 8px 25px 8px 25px;
        transition-duration: 0.3s;
    }
    
    .nav_btn:hover{
        color: rgb(255, 0, 200);
        transition-duration: 0.3s;
        background-color: rgb(255, 0, 255, 0.1);
    }  
}

@media screen and (min-width:1024px) {
    .top_banner {
        background-color: transparent;
        justify-content: flex-end;
        height: 2.2rem;
    }

    .nav_btn {
        font-size: 14px;
        font-weight: bolder;
        padding: 8px 25px 8px 25px;
        transition-duration: 0.3s;
    }
    
    .nav_btn:hover{
        color: rgb(255, 0, 200);
        transition-duration: 0.3s;
        background-color: rgb(255, 0, 255, 0.1);
    }  
}

