*{
    letter-spacing: 1.6px;
    color: white;
}

a {
    text-decoration: none;
}


.overall_body {
    width: 100vw;
    height: 100vh;
    display: flex;
}

.iconList {
    display: flex;
    justify-content: space-evenly;
}

.left_body{
    width: 30%;
    background-color: rgb(17, 17, 17);
    box-shadow: 0px px 20px 4px black;
}

.right_body {
    width: 70%;
    padding-left: 5px;
    background-color: rgb(25, 25, 25);
    overflow-x:hidden;
    overflow-y:auto;

}

.resume_profile {
    display: flex;
    align-items: center;
    justify-content: center;
}

.profile_picture{
    margin-top: 2rem;
    background-size: contain;
    height: 12rem;
    background-color: white;
    width: 12rem;
    border-radius: 100%;
    background-image: url("./index.png");
    background-position: center;
}

.lcontent {
    margin-top: 1rem;
    height: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

.name{
    width: fit-content;
    font-size: 3rem;
    margin: 20px;
    margin-top: 20px;
    font-weight: bold;
    color:rgb(255, 0, 255);
}

.education_body{
    height: fit-content;
    width: 50rem;
    margin: 20px;
    margin-top: 25px;
}

.edu_name{
    font-size: 2.7rem;
    margin-top: 3rem;
    font-weight: bold;
    color:rgb(255, 56, 255);
}

.inst_body {
    height: 5rem;
}

.inst_name {
    width: 60rem;
    display: flex;  
    justify-content: space-between;
    margin-top: 15px;
}

._name {
    font-size: 20px;
    font-weight: bold;
    margin-top: 5px;
}

.inst_details {
    margin-top: 2px;
    font-size: 15px;
    width: 45rem;
}

button {
    border: none;
    background-color: rgb(52, 52, 52);
    height: 1.7rem;
    border-radius: 4px;
    transition-duration: 0.3s;
    cursor: pointer;
    font-weight: bold;
}

button:hover {
    transition-duration: 0.3s;
    background-color: rgb(255, 0, 255)
}

li {
    line-height: 2;
}

@media screen and (min-width:320px) and (max-width:480px) {
    .profile_picture{
        height: 5rem;
        width: 5rem;
    }

    .left_body{
        width: 40%;
    }
    
    .right_body {
        width: 60%;    
    }

    .lcontent {
        font-size: 10px;
        letter-spacing: 1px;
    }
    .letters {
        font-size: 8.7px;
        letter-spacing: 0.5px;
        font-weight: bold;
    }
    ._download button{
        font-size: 8.7px;
        letter-spacing: 0.3px;
    }

    .name{
        font-size: 20px;
        margin:10px;
        margin-left: 1px;
    }

    .education_body{
        margin: 10px;
    }

    .edu_name{
        font-size: 15px;
        margin-top: 3rem;
    }

    .inst_name {
        width:12.3rem;
        margin-top: 5px;
    }

    ._name {
        font-size: 10px;
        font-weight: bold;
        margin-top: 5px;
    }
    
    .inst_details {
        margin-top: 5px;
        font-size: 8px;
        width: 11.6rem;
    }

    .education_body{
        margin: 1px;
    }

    .edu{
        width: 13rem;
    }
    li {
        line-height: 1.5;
        letter-spacing: 0.2px;
        margin-left: -2rem;
    }
}


