.skill_body {
    background-color: rgb(25, 25, 25);
    height: fit-content;
    min-height: 100vh;
}

.stack_body {
    display: flex;
}

.skillhead1{
    text-align: center;
    padding-top: 2.6rem;
    color:rgb(255, 0, 255);
    font-size: 2.2rem;
}

.skillhead2{
    font-size: 4rem;
    text-align: center;
    color: white;
    font-weight: bold;
    padding-bottom: 1.4rem;
}

.stack{
    width: 100%;
    height: fit-content;
}

.head_name {
    text-align: center;
    margin: 4rem;
    font-size: 1.7rem;
    color: white;
    letter-spacing: 1.2px;
    font-weight: bold;
}

.upper {
    display: flex;
    justify-content: space-around;
    margin: 1.3rem;
}

.sk {
    width: 4.7rem;
    height: 4.7rem;
    margin-top: 1.2rem;
    background-repeat: no-repeat;
    background-size: contain;
}

.c {
    background-image: url("./icons/C.png");
}

.c__ {
    background-image: url("./icons/C++.png");
}

.html {
    background-image: url("./icons/HTML.png");
}

.css {
    background-image: url("./icons/CSS.png");
}

.js {
    background-image: url("./icons/js.png");
}

.react {
    background-image: url("./icons/react.png");
}

.node{
    background-image: url("./icons/node.png");
}

.vs {
    background-image: url("./icons/VS.png");
}

.figma {
    background-image: url("./icons/Figma.png");
}

.git {
    background-image: url("./icons/Git.png");
}

.boot {
    background-image: url("./icons/Bootstrap.png");
}

.postman {
    background-image: url("./icons/Postman.png");
}

.docker {
    background-image: url("./icons/docker.png");
}

.linux{
    background-image: url("./icons/linux.png");
}

@media screen and (min-width:320px) and (max-width:480px) {
    .skill_body {
        background-color: rgb(25, 25, 25);
        height: fit-content;
        min-height: 100vh;
    }
    
    .stack_body {
        display: flex;
        flex-direction: column;
    }
    
    .skillhead1{
        font-size: 1.2rem;
    }
    
    .skillhead2{
        font-size: 1.4rem;
        margin-top: 5px;
    }
    
    .head_name {
        margin: 3rem;
        font-size: 1.2rem;
        letter-spacing: 1.9px;
    }

    .sk {
        width: 2.5rem;
        height: 2.5rem;
        margin-top: 1rem;
    }
    .btm_sk {
        margin-bottom: 3rem;
    }
}

@media screen and (min-width:481px) and (max-width:768px) {
    .skill_body {
        background-color: rgb(25, 25, 25);
        height: fit-content;
        min-height: 100vh;
    }
    
    .stack_body {
        display: flex;
        flex-direction: column;
    }
    
    .skillhead1{
        font-size: 1.5rem;
    }
    
    .skillhead2{
        font-size: 2rem;
        margin-top: 5px;
    }
    
    .head_name {
        margin: 3rem;
        font-size: 1.2rem;
        letter-spacing: 1.9px;
    }
    
    
    .sk {
        width: 3rem;
        height: 3rem;
        margin-top: 1rem;
        margin-bottom: 3rem;
    }   
    .btm_sk {
        margin-bottom: 3rem;
    }
}

@media screen and (min-width:769px) and (max-width:1023px) {
    .skill_body {
        background-color: rgb(25, 25, 25);
        height: fit-content;
        min-height: 100vh;
    }
    
    
    .skillhead1{
        font-size: 2rem;
    }
    
    .skillhead2{
        font-size: 3rem;
        margin-top: 5px;
    }
    
    .head_name {
        margin: 2rem;
        font-size: 1rem;
        letter-spacing: 1.9px;
    }

    .sk {
        width: 3rem;
        height: 3rem;
        margin-top: 3rem;
       
    }   
    .btm_sk {
        margin-bottom: 5rem;
    }
}