.description {
  font-family: 'Maven Pro', sans-serif;
  font-size: 3rem;
  font-weight: bold;
  letter-spacing: 3px;
  color: white;
  text-align: center;
}

@media screen and (min-width:320px) and (max-width:480px) {
  .description {
    font-size: 1.1rem;
  }
}

@media screen and (min-width:481px) and (max-width:768px) {
  .description {
    font-size: 1.3rem;
  }
}

@media screen and (min-width:769px) and (max-width:1023px) {
  .description {
    font-size: 1.5rem;
  }
}

