.contact_body{
    background-color: rgb(15, 15, 15);
    height: 100vh;
}

.contact_head1{
    text-align: center;
    padding-top: 2.6rem;
    color:rgb(255, 0, 255);
    font-size: 2.2rem;
}

.contact_head2{
    font-size: 4rem;
    text-align: center;
    color: white;
    font-weight: bold;
    padding-bottom: 1.4rem;
}

.contact_ {
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.container{
    width: 37.3rem;
    margin-top: 3.2rem;
    height: 1.5rem;
    color: white;
    font-size: 1.4rem;
    text-align: center;
    letter-spacing: 2px;
}

.copyright{
    color: rgb(87, 87, 87);
    margin-top: 6.5rem;
    text-align: center;
    width: 100vw;
}

@media screen and (min-width:320px) and (max-width:480px) {

    .contact_body {
        background-color: rgb(25, 25, 25);
        height: fit-content;
        min-height: 100vh;
    }
    
    .contact_head1{
        font-size: 1.2rem;
    }
    
    .contact_head2{
        font-size: 1.4rem;
        margin-top: 5px;
    }
    .container{
        width: 30rem;
        margin-top: 4rem;
        height: fit-content;
        font-size: 17px;
        font-weight: bold;
    }
    
    .copyright{
        margin-top: 15rem;
    }
}

@media screen and (min-width:481px) and (max-width:768px) {
    .contact_body {
        background-color: rgb(25, 25, 25);
        height: fit-content;
        min-height: 100vh;
    }
    
    .contact_head1{
        font-size: 1.5rem;
    }
    
    .contact_head2{
        font-size: 2rem;
        margin-top: 5px;
    }
    .container{
        width: 30rem;
        margin-top: 4rem;
        height: fit-content;
        font-size: 21px;
        font-weight: bold;
    }
    
    .copyright{
        margin-top: 14rem;
    }
}

@media screen and (min-width:769px) and (max-width:1023px) {
    .contact_body {
        background-color: rgb(25, 25, 25);
        height: fit-content;
        min-height: 100vh;
    }
    
    .contact_head1{
        font-size: 2rem;
    }
    
    .contact_head2{
        font-size: 3rem;
        margin-top: 5px;
    }
    .container{
        width: 30rem;
        margin-top: 4rem;
        height: fit-content;
        font-size: 21px;
        font-weight: bold;
    }
    
    .copyright{
        margin-top: 12rem;
    }
}

