.background {
  height: 100vh;
  display: flex;
  background-image: url("./133Z_2106.w026.n002.486B.p1.486-min.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.black_bar {
  width: 7rem;
  height: 100vh;
  background-color: rgb(15, 15, 15);
}

.firstpage_body {
  background-image: linear-gradient(
    20deg,
    rgb(15, 15, 15) 20%,
    rgb(127, 0, 127)
  );
  opacity: 0.9;
  margin: none;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.my_description {
  font-family: "Maven Pro", sans-serif;
  font-weight: bold;
  color: white;
  text-align: center;
  height: 10rem;
  width: 45rem;
}

.icon_links {
  position: absolute;
  left: 2px;
}

.second {
  height: 100rem;
}

a {
  color: rgb(255, 255, 255);
}

a:hover {
  cursor: pointer;
  transition-duration: 0.3s;
  color: rgb(255, 60, 255);
}

.iconl {
  margin: 35% 0% 2.5rem 1.5rem;
  color: rgb(255, 255, 255);
  font-size: 2.6rem;
}

.responsive_icons {
  display: none;
}

@media screen and (min-width: 320px) and (max-width: 480px) {
  .black_bar {
    display: none;
  }

  .icon_links {
    display: none;
  }

  .responsive_icons {
    display: block;
    position: absolute;
    display: flex;
    bottom: 7rem;
  }

  .resp_icon {
    margin: 14px;
  }
  .resp_icon > a {
    font-size: 20px;
  }

  a {
    color: rgb(255, 255, 255);
  }

  a:hover {
    cursor: pointer;
    transition-duration: 0.3s;
    color: rgb(255, 60, 255);
  }
}

@media screen and (min-width: 481px) and (max-width: 768px) {
  .black_bar {
    display: none;
  }

  a {
    color: rgb(255, 255, 255);
  }

  a:hover {
    cursor: pointer;
    transition-duration: 0.3s;
    color: rgb(255, 60, 255);
  }

  .icon_links {
    display: none;
  }

  .responsive_icons {
    display: block;
    position: absolute;
    display: flex;
    bottom: 7rem;
  }

  .resp_icon {
    margin: 30px;
  }
  .resp_icon > a {
    font-size: 30px;
  }
}

@media screen and (min-width: 769px) and (max-width: 1023px) {
  .black_bar {
    display: none;
  }

  .icon_links {
    display: none;
  }

  .responsive_icons {
    display: block;
    position: absolute;
    display: flex;
    bottom: 3.5rem;
  }

  .resp_icon {
    margin: 40px;
  }

  .resp_icon > a {
    font-size: 40px;
  }

  a {
    color: rgb(255, 255, 255);
  }

  a:hover {
    cursor: pointer;
    transition-duration: 0.3s;
    color: rgb(255, 60, 255);
  }
}
