*{
    font-family: 'Maven Pro', sans-serif;
}

.cover {
    background-color: rgb(25, 25, 25);
    height: fit-content;
    min-height: 100vh;
}

.heading{
    background-color: rgb(15, 15, 15);
    box-shadow: 0px 0px 20px 4px black;
}

.about_body {
    height: fit-content;
    min-height: 100vh;
    background: linear-gradient(rgb(25,25,25), rgb(35,35,35));
}

.head1{
    text-align: center;
    padding-top: 2.6rem;
    color:rgb(255, 0, 255);
    font-size: 2.2rem;
}

.head2{
    font-size: 4rem;
    text-align: center;
    color: white;
    font-weight: bold;
    padding-bottom: 1.4rem;
}

.describe_body{
    color: white;
    display: flex;
    justify-content: center;
    padding-top: 3rem;
}

.profile_pic {
    height: 20rem;
    border: 1px solid rgb(255, 0, 255);
    width: 15rem;
    background-image: url("./12\(1\).jpg");
    background-position: center;
    background-size: cover;
    box-shadow: 0px 0px 20px 4px black;
    border-radius: 5px 5px 5px 5px;
}

.describe_para {
    width: 45rem;
    color: rgb(57, 57, 57);
    height: 15rem;
    margin: 3rem;
    text-align: justify;
    font-size: 1.5rem;
    padding: 1.2rem;
    letter-spacing: 2px;
    line-height: 1.6;
}

.describe_para > p{
    color: rgb(172, 172, 172);
    font-weight: bold;
}

@media screen and (min-width:320px) and (max-width:480px) {

    .cover {
        background-color: rgb(25, 25, 25);
        height: fit-content;
        min-height: 100vh;
    }
    
    .head1{
        font-size: 1.2rem;
    }
    
    .head2{
        font-size: 1.4rem;
        margin-top: 5px;

    }
    
    .describe_body{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .profile_pic {
        height: 15rem;
        border: 1px solid rgb(255, 0, 255);
        width: 13rem;
    }
    
    .describe_para {
        width: 18rem;
        font-size: 1rem;
        line-height: 1.4;
    }
}

@media screen and (min-width:481px) and (max-width:768px) {
    .cover {
        background-color: rgb(25, 25, 25);
        height: fit-content;
        min-height: 100vh;
    }

    .head1{
        font-size: 1.3rem;
    }
    
    .head2{
        font-size: 1.5rem;
        margin-top: 5px;

    }
    
    .describe_body{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .profile_pic {
        height: 20rem;
        width: 17rem;
    }
    
    .describe_para {
        width: 27rem;
        font-size: 1rem;
        line-height: 1.4;
    }
}

@media screen and (min-width:769px) and (max-width:1023px) {
    .cover {
        background-color: rgb(25, 25, 25);
        height: fit-content;
        min-height: 100vh;
    }


    .head1{
        font-size: 1.6rem;
    }
    
    .head2{
        font-size: 2.3rem;
        margin-top: 5px;

    }
    
    .describe_body{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .profile_pic {
        height: 20rem;
        width: 17rem;
    }
    
    .describe_para {
        width: 32rem;
        font-size: 1.3rem;
        line-height: 1.4;
    }

}

